
import {Nav, Navbar} from 'react-bootstrap'
import { Link, Route, Switch } from "react-router-dom";
import './Header.css'

function Header(props) {
    
    
    return(
        <>            
            <Navbar bg="dark" variant="dark" className="justify-content-center customNavbarBackground">
                
                    <Nav className="mr-auto">
                        <Link  to="/home" className="customNavItemPadding">BCS</Link>
                        <Link  to="/servicesInfo" className="customNavItemPadding">Usługi</Link>
                        <Link  to="/contact" className="customNavItemPadding">Kontakt</Link>
                        <Link  to="/location" className="customNavItemPadding">Mapa</Link>
                    </Nav>
                 
            </Navbar>
        </>
    );
  }

  export default Header;